import { Component, Input, ElementRef, Renderer, ViewChild, Output, EventEmitter } from '@angular/core';

/**
 * Generated class for the ShrinkingHeaderComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'shrinking-header',
  templateUrl: 'shrinking-header.html'
})
export class ShrinkingHeader {

  @Input('scrollArea') scrollArea: any;
  @Input('headerHeight') headerHeight: number;
  @ViewChild('#imgHeader') imgHeader: any;
  newHeaderHeight: any;
  hide: boolean;
  
  @Output() messageEvent = new EventEmitter<boolean>();

  sendMessage() {
    this.messageEvent.emit(this.hide);
    console.log("evento: " + this.hide)
  }

  constructor(public element: ElementRef, public renderer: Renderer) {
    
  }
 
  ngAfterViewInit(){
 
    this.renderer.setElementStyle(this.element.nativeElement, 'height', this.headerHeight + 'px');
 
    this.scrollArea.ionScroll.subscribe((ev) => {
      this.resizeHeader(ev);
    });
 
  }
 
  resizeHeader(ev){
    
    ev.domWrite(() => {
 
      this.newHeaderHeight = this.headerHeight - ev.scrollTop;
      console.log("headeheight: "+this.newHeaderHeight)
      if(this.newHeaderHeight < 0){
        this.newHeaderHeight = 0;
      }
      if(this.newHeaderHeight < 69){ 
        this.hide=false;
        this.sendMessage();
      }  
      if(this.newHeaderHeight >= 69){
        this.hide=true;
        this.sendMessage();
        console.log("header > 20")
      }  
      this.renderer.setElementStyle(this.element.nativeElement, 'height', this.newHeaderHeight + 'px');
 
    });
 
  }
}



