import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';

import { ChartsModule } from 'ng2-charts';
import { HTTP } from '@ionic-native/http';
import { HttpClientModule } from '@angular/common/http';


import { ShrinkingHeader } from '../components/shrinking-header/shrinking-header'
import { RadarChartComponent } from '../components/radar-chart/radar-chart';
import { ComponentsItemSvgComponent } from '../components/components-item-svg/components-item-svg'
import { SegmentHomeProfile } from '../components/segment-home-profile/segment-home-profile';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ShrinkingHeader,
    RadarChartComponent,
    ComponentsItemSvgComponent,
    SegmentHomeProfile
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    ChartsModule,
    HttpClientModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    RadarChartComponent,
    ComponentsItemSvgComponent
  ],
  providers: [
    StatusBar,
    SplashScreen,
    HTTP,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



